/* Form components: BOC */
input[type="checkbox"],
input[type="radio"] {
  position: absolute !important;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  font-size: 0.1em;
}
input[type="checkbox"] + label,
input[type="radio"] + label {
  padding-left: 25px;
  display: inline-block;
  position: relative;
  margin: 5px 20px 5px 0;
}
input[type="checkbox"] + label:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 4px;
  left: 0;
  border: 1px solid #4a4a4a;
  background: #fff;
}
input[type="checkbox"]:checked + label:after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background-color: #001978;
  /*border-bottom: 2px solid #01385f;
    border-left: 2px solid #01385f;*/
  position: absolute;
  top: 5px;
  left: 5px;
}
input[type="radio"] + label:before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 4px;
  left: 0;
  border: 1px solid #001978;
  background: #fff;
  border-radius: 50%;
}
input[type="radio"]:checked + label:after {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  background: #001978;
  position: absolute;
  border-radius: 50%;
  top: 7px;
  left: 3px;
}

.lexon-select-like-custom-trigger {
  height: 45px;
  width: 350px;
  border: 1px solid #d2d2d2;
  background-color: #ffffff;
  outline: none;
  color: #7c868c;
  font-size: 15px;
  text-align: left;
  position: relative;
  padding-left: 10px;
}
.lexon-select-like-custom-trigger:focus {
  outline: none;
}
.lexon-select-like-custom-trigger span {
  font-size: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
  color: #001978;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.lexon-select-like-custom-trigger.opened span {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}
.lexon-select-like-custom-list-container {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  height: 0;
  width: 350px;
  position: absolute;
  z-index: 2;
  overflow: hidden;
}
.lexon-select-like-custom-list-container.opened {
  height: 230px;
  overflow: visible;
}
.lexon-select-like-custom-list {
  height: 230px;
  width: 350px;
  border: 1px solid #d2d2d2;
  background-color: #ffffff;
  box-shadow: 0 0 4px 1px rgba(102, 175, 233, 0.6);
  position: absolute;
  z-index: 1;
  padding: 5px;
  overflow: scroll;
  overflow-x: hidden;
}
.lexon-select-like-custom-list li {
  border-bottom: solid 1px #001978;
  height: 43px;
  color: #7c868c;
  font-size: 15px;
  line-height: 43px;
  padding: 0 10px;
  margin: 0 2px;
}
.lexon-select-like-custom-list li:last-child {
  border-bottom: none;
}
.lexon-select-like-custom-list li span {
  color: #7c868c;
}
.lexon-select-like-custom-list li.selected,
.lexon-select-like-custom-list li:hover {
  background-color: #e5e8f1;
  color: #001978;
  cursor: pointer;
}
